<template>
  <div class="ship">
    <div class="table_con">
      <el-table
        max-height="750"
        :data="tableData"
        style="width: 100%; margin: auto; margin-top: 20px"
        border
        v-loading="loading"
        element-loading-text="拼命加载中,请稍等"
      >
        <el-table-column prop="order_id" label="订单号" width="200"></el-table-column>
        <el-table-column label="详细地址" width="200">
          <template slot-scope="scope">
            <el-input v-model="scope.row.address"></el-input>
          </template>
        </el-table-column>
        <el-table-column prop="country_code" label="国家" width="80"></el-table-column>
        <el-table-column prop="station" label="站点" width="80"></el-table-column>
        <el-table-column prop="reason" label="失败原因" width="120"></el-table-column>
        <el-table-column label="城市" width="230">
          <template slot-scope="scope">
            <el-input v-model="scope.row.city"></el-input>
          </template>
        </el-table-column>
        <el-table-column label="收货人姓名" width="230">
          <template slot-scope="scope">
            <el-input v-model="scope.row.name"></el-input>
          </template>
        </el-table-column>
        <el-table-column label="电话" width="120">
          <template slot-scope="scope">
            <el-input v-model="scope.row.tel"></el-input>
          </template>
        </el-table-column>
        <el-table-column label="邮编" width="120">
          <template slot-scope="scope">
            <el-input v-model="scope.row.zip_code"></el-input>
          </template>
        </el-table-column>
      </el-table>
      <el-button class="submitbtn" type="primary" @click="onSubmit">提交</el-button>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      tableData: [
        {
          order_id: "",
          country_code: "",
          station: "",
          reason: "",
          city: "",
          address: "",
          name: "",
          tel: "",
          zip_code: "",
          id: ""
        }
      ],
      loading: false
    };
  },
  props: ["type"],
  mounted() {
    this.get_data();
  },
  methods: {
    onSubmit() {
      let url = "server/order_management/single_order_deliver/";
      let formData = new FormData();
      let order_id = [];
      let country_code = [];
      let city = [];
      let address = [];
      let name = [];
      let tel = [];
      let zip_code = [];
      let id = [];
      let is_ajax = true;
      this.tableData.forEach((item, index) => {
        if (
          item.city == "" ||
          item.address == "" ||
          item.name == "" ||
          item.tel == "" ||
          item.zip_code == ""
        ) {
          is_ajax = false;
          return alert("请填写完整");
        }
      });
      if (is_ajax == true) {
        this.tableData.forEach((item, index) => {
          order_id.push(item.order_id);
          country_code.push(item.country_code);
          city.push(item.city);
          address.push(item.address);
          name.push(item.name);
          tel.push(item.tel);
          zip_code.push(item.zip_code);
          id.push(item.id);
        });
        formData.append("order_id", order_id);
        formData.append("country_code", country_code);
        formData.append("city", city);
        formData.append("address", address);
        formData.append("name", name);
        formData.append("tel", tel);
        formData.append("zip_code", zip_code);
        formData.append("id", id);
        formData.append("err_order", "YS");
        this.$axios.post(this._url2 + url, formData).then(res => {
          console.log("编辑数据提交", res.data);
          if (res.data.code === 200) {
            alert(res.data.mag);
            window.location.reload();
          } else {
            alert(res.data.msg);
          }
        });
      }
    },

    //获取表格数据
    get_data() {
      let url = "server/order_management/address_overlength/";
      let that = this;
      this.loading = true;
      this.$axios.post(this._url2 + url).then(res => {
        console.log(res.data);
        if (res.data.code === 200) {
          that.tableData = res.data.data;
          that.loading = false;
        } else {
          alert(res.data.msg);
        }
      });
    }
  }
};
</script>
<style scoped>
.ship .el-form .submitbtn {
  width: 100%;
  margin-left: 0 !important;
}
.ship .el-form {
  border: 1px solid #e5e5e5;
  padding: 40px 50px 20px 0px;
  border-radius: 6px;
  margin-bottom: 20px;
}
.ship .easywarehouse.el-form {
  text-align: center;
  border: 1px solid #e5e5e5;
  border-radius: 6px;
  padding-top: 20px;
}
.ship .easywarehouse.el-form > .el-form-item {
  width: 350px;
  margin: 0px auto;
}
.ship .el-form-item__content {
  text-align: center;
  display: flex;
  align-items: center;
  margin-top: 3px;
}
.ship .el-form-item__content > .el-button.el-button--primary {
  margin-left: -200px;
}
.card_com .ship .el-select {
  width: 100%;
}
</style>
<style scoped>
.ship > .shipOption {
  font-size: 16px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.ship > .shipOption > div {
  background: rgba(255, 255, 255, 0.9);
  height: 60px;
  line-height: 60px;
  padding: 0 30px;
  margin: 0 30px 20px;
}
.ship > .shipOption input[type="file"] {
  width: 170px;
}
.div_column {
  display: flex;
  justify-content: space-around;
  width: 60%;
  margin: auto;
}
</style>
<style>
.el-textarea .el-textarea__inner {
  resize: none;
}
</style>



